.videoblock {
    padding: 50px 0;
    background-color: #fff;
    .videoblock__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        .video {
            width: 100%;
            height: auto;
        }
    }
    
}
.first {
    padding: 0 0 50px 0;
}
.third {
    padding: 0 0 50px 0;
}
.fourth {
    padding: 0;
}
.sixth {
    padding: 0;
}
@media screen and (max-width: 1024px) {
    .videoblock {
        .videoblock__title {
            font-size: 56px;
            margin-bottom: 20px;
        }
    }
}
@media screen and (max-width: 425px) {
    .videoblock {
        .videoblock__title {
            font-size: 36px;
            margin-bottom: 20px;
        }
    }
}